import { UtilsService } from 'src/app/shared/utils.service';
import { HttpErrorResponse, HttpEventType } from '@angular/common/http';
import { SpinnerService } from './../../../../shared/spinner.service';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { DataConnectionComponent } from '../data-connection.component';
import { MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import { DataService } from '../../data.service';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { environment } from 'src/environments/environment';
import { toasterMsg } from 'src/app/shared/static-data';
import { takeUntil } from 'rxjs/operators';
@Component({
  selector: 'app-file-upload-form',
  templateUrl: './file-upload-form.component.html',
  styleUrls: ['./file-upload-form.component.scss'],
})
export class FileUploadFormComponent implements OnInit {
  @Output() goBackFlag = new EventEmitter<boolean>();
  @Input() dataGroupsArray: Array<any>;
  @Input() currentDataGroup: string;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  // Progress spinner settings
  // color: ThemePalette = "primary";
  //color = 'primary'
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  exportProjectDiameter = 50;

  fileUploadForm: FormGroup;
  fileToUpload: any;
  project: any;
  selectedFolder: any;
  fileUploadApi: string;
  user: any;
  //configure it to change the file size - this is in mb.
  allowedPSFileUploadSize: number;
  progress: number;
  serverBaseUrl = environment.serverBaseUrl;
  percentDone;
  constructor(
    private _fb: FormBuilder,
    private _data: DataService,
    private _localStorage: LocalStorageService,
    private _connectionDialogRef: MatDialogRef<DataConnectionComponent>,
    private _toaster: ToastrService,
    private _snackbar: MatSnackBar,
    private _spinner: SpinnerService,
    private _utils: UtilsService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.allowedPSFileUploadSize = this.user?.psFeatures?.allowedPSFileUploadSize || 50;
    this.fileUploadForm = this._fb.group({
      encoding: ['utf_8', Validators.required],
      selectFolder: ['', Validators.required],
      dataFolderName: ['', Validators.required],
      parentFolderName: ['', Validators.required],
    });
  }

  /**
   * Sets the default values
   */
  ngOnInit(): void {
    this.fileUploadForm.get('selectFolder').setValue(this.currentDataGroup);
  }

  /**
   * Navigates to the data source type selection page
   */
  goBack(): void {
    this.goBackFlag.emit(true);
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any, formValue: any): void {
    const ext = inputFile[0].name.split('.').pop();
    if (inputFile && ['csv', 'xlsx', 'xls', 'pkl', 'pk', 'pl', 'pickle'].includes(ext)) {
      if (inputFile[0].size < this.allowedPSFileUploadSize * 1024 * 1024) {
        this.fileToUpload = inputFile[0];
        this.createConnection(formValue);
      } else {
        this._snackbar.open(
          'File size should be less than ' + this.allowedPSFileUploadSize + 'Mb',
          'OK',
          {
            horizontalPosition: 'right',
            verticalPosition: 'bottom',
          }
        );
      }
    } else {
      this._snackbar.open(`.${ext} file type is not supported`, 'OK');
    }
  }

  /**
   * Uploads the file and create a connection
   * @param formValue
   */
  createConnection(formValue: any): void {
    this._spinner.showSpinner();
    let dataGroupId: string;
    let parentFolder: string;

    if (formValue.selectFolder === 'Add') {
      dataGroupId = formValue.dataFolderName;
      parentFolder = formValue.parentFolderName;
    } else {
      dataGroupId = formValue.selectFolder;
    }

    let reqObj = {
      encoding: formValue.encoding,
      projectId: this.project._id,
      dataGroupId: dataGroupId,
      dataFolderName: dataGroupId,
      parentFolderName: parentFolder,
      file: this.fileToUpload,
    };
    this._data
      .uploadFile(reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (resp) => {
          if (resp.type === HttpEventType.UploadProgress) {
            this.percentDone = Math.round((100 * resp.loaded) / resp.total);
          }
          if (this.percentDone === 100) {
            this._utils.showSnackBar(toasterMsg.fileUpload);
            this._spinner.stopSpinner();
            resp.type === 4 && resp.status === 200 ? this._connectionDialogRef.close() : null;
          }
        },
        (err: HttpErrorResponse) => {
          this._toaster.error(err.error.message || 'Server Error', 'Data Connection');
          this._spinner.stopSpinner();
          this._utils.dismissSnackBar();
        }
      );
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
