import { DataSetComponent } from '../data-set.component';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { Subscription } from 'rxjs';
import { DataService } from '../../data.service';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { GridOptions } from 'ag-grid-community';
import { SpinnerService } from 'src/app/shared/spinner.service';

@Component({
  selector: 'app-data-set-dialog',
  templateUrl: './data-set-dialog.component.html',
  styleUrls: ['./data-set-dialog.component.scss']
})
export class DataSetDialogComponent implements OnInit {

  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  gridOptions: GridOptions;
  resizeEvent = 'resize.ag-grid';
  private gridApi;
  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  exportProjectDiameter = 50;

  /** Subscription variables */
  dataConnection$: Subscription;

  joinTypeArray: Array<string> = ['inner', 'left', 'right', 'outer'];
  mergeForm: FormGroup;
  showPreview: boolean = false;
  showReport: boolean = false;
  showMergeForm: boolean = false;
  modalHeading: string;
  project: any;
  parentFile: any;
  showSpinner: boolean = false;
  dataSetList: Array<any> = [];
  mergeType: string = 'row';
  avalialbleColumnsToMerge: Array<any>;
  commonColumns: Array<any>;
  mergeDataSet: any;
  gridColumnApi: any;
  previewrowData: any;
  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _spinner: SpinnerService, private _fb: FormBuilder, private _data: DataService, private _toaster: ToastrService, private _dataSetDialogRef: MatDialogRef<DataSetComponent>, private _localStorage: LocalStorageService, public _utils: UtilsService
  ) {
    /** Form instances */
    this.mergeForm = this._fb.group({
      selectedDataset: ['', Validators.required],
      joinType: ['', Validators.nullValidator],
      commonColumn: ['', Validators.nullValidator],
      columnsToMerge: ['', Validators.nullValidator]
    });
  }

  /**
   * Assigns the references after the view is initialized
   */
  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  /**
   * Initial settings for the dialog
   */
  ngOnInit(): void {
    if (this.dialogData.type === 'preview') {
      this.showPreview = true;
      this.modalHeading = 'Data Preview';
      console.log(this.dialogData);
      this.previewData();
    } else {
      this.showMergeForm = true;
      this.modalHeading = 'Merge Data';
      this.project = this._localStorage.getItem('project');
      this.listAllProjectFileList(this.dialogData.data);
    }
  }
  previewData() {
    this._spinner.showSpinner();
    let columnDefs = [];
    this._data.myFileData(this.dialogData.reqObj.pid, this.dialogData.dataId, this.dialogData.reqObj, true).subscribe(
      (res: any) => {
        if (res) {
          for (let key in res.preview[0]) {
            columnDefs.push({
              headerName: key,
              field: key,
            });
          }
          console.log(columnDefs);
          this.gridOptions = {
            headerHeight: 40,
            columnDefs: columnDefs,
            // rowData: res.preview,
            defaultColDef: { resizable: true },
          };

          this.previewrowData = res.preview;
          this._spinner.stopSpinner();
        } else {
          this._spinner.stopSpinner();
          this._toaster.error('Sorry! Cannot preview the files data.', 'Data Set');
        }
      },
      (err: Error) => {
        this.showSpinner = false;
        this._toaster.error('Server Error', 'Data Set');
      }
    );
  }
  /**
   * Fetches list of all the files associated with a project
   * @param fileData 
   */
  listAllProjectFileList(fileData: any): void {
    this.dataConnection$ = this._data.getAllProjectFileList(this.project._id).subscribe((res: any) => {
      if (res) {
        this.parentFile = res.find((data: any) => data._id === fileData._id ? true : false);

        // Filters the files list to remove selected file from response list
        this.dataSetList = res.filter((item: any) => item._id !== fileData._id);
      }
    }, (err: Error) => {
      this._toaster.error('Server Error', 'Data Sets');
    });
  }

  /**
   * Calls when the data set is changed
   * @param dataSetId 
   */
  dataSetChanged(dataSetId: string): void {
    this.mergeDataSet = this.dataSetList.find((data: any) => data._id === dataSetId ? true : false);
    this.avalialbleColumnsToMerge = this.mergeDataSet.fileSchema;
    this.commonColumns = this.parentFile.fileSchema.filter((value: any) => {
      let found = this.mergeDataSet.fileSchema.find((item: any) => item.colName === value.colName);
      return found;
    });
  }

  /**
   * Calls when the merge type is changed for form validations
   * @param type 
   */
  mergeTypeChanged(type: string): void {
    if (type === 'column') {
      this.mergeForm.get('joinType').setValidators(Validators.required);
      this.mergeForm.get('commonColumn').setValidators(Validators.required);
      this.mergeForm.get('columnsToMerge').setValidators(Validators.required);
      this.mergeForm.get('joinType').updateValueAndValidity();
      this.mergeForm.get('commonColumn').updateValueAndValidity();
      this.mergeForm.get('columnsToMerge').updateValueAndValidity();
    } else {
      this.mergeForm.get('joinType').clearValidators();
      this.mergeForm.get('commonColumn').clearValidators();
      this.mergeForm.get('columnsToMerge').clearValidators();
      this.mergeForm.get('joinType').updateValueAndValidity();
      this.mergeForm.get('commonColumn').updateValueAndValidity();
      this.mergeForm.get('columnsToMerge').updateValueAndValidity();
    }
  }

  /**
   * Creates a new merged file
   * @param formValue 
   */
  onSubmitMerge(formValue: any): void {
    this.showSpinner = true;
    formValue.originalFilePath = this.parentFile.filename;
    formValue.pid = this.project._id;
    formValue.dataGroupId = this.dialogData.data.datagroup_id;
    formValue.mergeFilePath = this.mergeDataSet.filename;
    formValue.parentDatasetTasks = this.parentFile.tasks;
    formValue.mergeDatasetTasks = this.mergeDataSet.tasks;
    formValue.mergeType = this.mergeType;

    if (this.mergeType === 'row') {
      delete formValue.columnsToMerge;
      delete formValue.commonColumn;
      delete formValue.joinType;
    }

    this.dataConnection$ = this._data.mergeDataSet(this.project._id, formValue).subscribe((res: any) => {
      if (res) {
        this.closeModal(res);
        this.showSpinner = false;
        this._toaster.info('Data merging started...', 'Data Sets');
      } else {
        this.showSpinner = false;
        this._toaster.error('Sorry! Cannot merge the data set.', 'Data Sets');
      }
    }, (err: Error) => {
      this.closeModal();
      this.showSpinner = false;
      this._toaster.error(err.message, 'Data Sets');
    });
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this._dataSetDialogRef.close(data);
  }
  /**
    * Called when ng-grid is rendered in dom
    * @param event
    */
  gridReady(event: any) {
    event.api.setDomLayout("autoHeight");
    this.gridApi = event.api;
    this.gridApi.setColumnDefs(this.gridOptions.columnDefs);
    this.gridApi.setRowData(this.previewrowData);
    this.gridApi.refreshCells({ force: true });

  }
  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.dataConnection$) {
      this.dataConnection$.unsubscribe();
    }
  }
}
