import { SpinnerService } from './../../../../shared/spinner.service';
import { Router } from '@angular/router';
import { CreateConnectionService } from './../../../dashboard/create-connection/create-connection.service';
import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { ToastrService } from 'ngx-toastr';
import { DataConnectionComponent } from '../data-connection.component';
import { Subject, Subscription } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { DataService } from '../../data.service';
import { environment } from 'src/environments/environment';
import { HttpErrorResponse } from '@angular/common/http';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-database-form',
  templateUrl: './database-form.component.html',
  styleUrls: ['./database-form.component.scss'],
})
export class DatabaseFormComponent implements OnInit, OnDestroy {
  @Output() goBackFlag = new EventEmitter<boolean>();
  @Input() dataGroupsArray: Array<any>;
  @Input() schedularsArray: Array<any>;
  @Input() sourceType: string;
  @Input() data: any;
  @Input() currentDataGroup: string;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  exportProjectDiameter = 50;

  /** Subscription variables */
  connection$: Subscription;
  unsubscribe$ = new Subject<boolean>();

  dbConnectionForm: FormGroup;
  fileToUpload: any;
  project: any;
  selectedFolder: string;
  fileUploadApi: string;
  dbConns: Array<any>;
  hidePassword: boolean = true;
  isPulling: boolean = true;
  isEdit: boolean = false;
  connectionIdToUpdate: string;
  connectionDetails: any;
  dataschedule: string;
  // restriction variables
  appType: string = environment.appType;
  user: any;
  allowedNoOfRows: number = 0;

  constructor(
    private _fb: FormBuilder,
    private _connection: CreateConnectionService,
    private _toaster: ToastrService,
    private _connectionDialogRef: MatDialogRef<DataConnectionComponent>,
    private _data: DataService,
    private _localStorage: LocalStorageService,
    private _router: Router,
    private _spinner: SpinnerService
  ) {
    this.dbConnectionForm = this._fb.group(
      {
        connectionName: [
          '',
          [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_\\- ]+$')],
        ],
        connection: ['', Validators.required],
        database: ['', Validators.required],
        query: ['', Validators.required],
        //predictsensePwd: ['', Validators.required],
        dataGroupId: ['', Validators.required],
        parent_id: ['', Validators.nullValidator],
        dataGroupName: ['', Validators.nullValidator],
        scheduleId: [''],
      },
      {
        validator: this.validateQuery,
      }
    );
  }

  /**
   * Initial settings for this page
   */
  async ngOnInit(): Promise<any> {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.allowedNoOfRows = this.user?.psFeatures?.allowedNoOfRows;
    await this.getRequiredConnectionList();
    /** Prefills the connection form as per the operation - edit/create */
    this.prefillConnectionForm(this.data);
  }

  /**
   * Fetches all required connections list
   */
  async getRequiredConnectionList(): Promise<any> {
    if (this.sourceType === 'snowflake' || this.sourceType === 'bigquery') {
      this.dbConns = await this._connection
        .getConnectionsList(this.sourceType)
        .pipe(takeUntil(this.unsubscribe$))
        .toPromise();
    } else {
      this.dbConns = await this._connection
        .getDbConnectionList()
        .pipe(takeUntil(this.unsubscribe$))
        .toPromise();
      this.dbConns = this.dbConns?.filter((item: any) => item.source === this.sourceType);
    }
  }

  /**
   * Prefills the connection form for edit and create functionality
   * @param connectionData selected connection details
   */
  prefillConnectionForm(connectionData: any): void {
    if (connectionData) {
      this.isEdit = true;
      this.connectionIdToUpdate = connectionData._id;
      this._data.getSingleDataConnection(this.project._id, connectionData._id).subscribe(
        (res: any) => {
          if (res) {
            this.connectionDetails = res;
            this.dbConnectionForm.patchValue({
              connectionName: res.connectionName,
              connection: this.dbConns.filter((item) => item._id === res.databaseConnectionId)[0],
              database: res.databaseName,
              query: res.query,
              scheduleId: res.scheduleId,
            });
            this.dbConnectionForm.get('dataGroupId').clearValidators();
            this.dbConnectionForm.get('dataGroupId').updateValueAndValidity();
           // this.dbConnectionForm.get('predictsensePwd').clearValidators();
           // this.dbConnectionForm.get('predictsensePwd').updateValueAndValidity();
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Connection');
        }
      );
    } else {
      this.dbConnectionForm.reset();
      this.dbConnectionForm.get('dataGroupId').setValue(this.currentDataGroup);
      this.isEdit = false;
    }
  }

  /**
   * Handles the connection type whether DB or SFTP connection
   * @param formValue connection details
   */
  onSubmitConnection(formValue: any): void {
    if (this.isEdit) {
      this.updateConnectionHandler(formValue);
    } else {
      this.createConnectionHandler(formValue);
    }
  }

  /**
   * Updates the connection details in DB
   * @param formData
   */
  updateConnectionHandler(formData: any): void {
    this.connectionDetails.databaseConnectionId = formData.connection._id;
    this.connectionDetails.connectionName = formData.connectionName;
    this.connectionDetails.databaseName = formData.database;
    this.connectionDetails.query = formData.query;
    this.connectionDetails.projectId = this.project._id;

    /** Logic to update schedulars when we edit */
    if (formData.scheduleId == 'None') {
      this.connectionDetails.statusData = 'None';
    }
    if (this.dataschedule === undefined) {
      this.connectionDetails.scheduledata = 'noupdate';
    } else {
      this.connectionDetails.scheduledata = this.dataschedule;
    }
    this.connectionDetails.scheduleId
      ? (this.connectionDetails.oldScheduleId = this.connectionDetails.scheduleId)
      : null;
    formData.scheduleId ? (this.connectionDetails.scheduleId = formData.scheduleId) : null;

    this._data
      .updateDataConnection(this.project._id, this.connectionDetails._id, this.connectionDetails)
      .subscribe(
        (res: any) => {
          if (res) {
            this.closeModal(res);
            this._toaster.success(
              `${this.sourceType.toUpperCase()} connection updated successfully`,
              'Data Connection'
            );
          }
        },
        (err: Error) => {
          this._toaster.success('Server Error', 'Data Connection');
        }
      );
  }

  /**
   * Pull the data from the db
   */
  createConnectionHandler(formData: any): void {
    formData.scheduleId ? formData.scheduleId : delete formData.scheduleId;
    formData.dataGroupName ? formData.dataGroupName : delete formData.dataGroupName;
    formData.parent_id ? formData.parent_id : delete formData.parent_id;

    let reqObj: object = {
      ...formData,
      source: this.sourceType,
      isPulling: this.isPulling,
    };
    this._data.pullData(this.project._id, reqObj).subscribe(
      (res: any) => {
        if (res) {
          this.closeModal(res);
          this._toaster.success(
            `${this.sourceType.toUpperCase()} connection created successfully`,
            'Data Connection'
          );
        }
      },
      (err: HttpErrorResponse) => {
        this._toaster.error(err.error.message || 'Server Error', 'Data Connection');
      }
    );
  }

  /**
   * Calls whenever the schedular is changed
   * @param event
   */
  schedularChanged(scheduleId: any): void {
    if (!this.connectionDetails?.scheduleId) {
      this.dataschedule = 'new';
    } else if (this.connectionDetails.scheduleId !== scheduleId) {
      this.dataschedule = 'update';
    } else {
      this.dataschedule = 'noupdate';
    }
  }

  /**
   * Navigates to the data source type selection page
   */
  goBack(): void {
    this.goBackFlag.emit(true);
  }

  /**
   * Call when the folder selection is changed
   */
  onFolderChange(folderName: string) {
    this.selectedFolder = folderName;
    if (this.selectedFolder === 'Add') {
      this.dbConnectionForm.get('dataGroupName').setValidators(Validators.required);
      this.dbConnectionForm.get('dataGroupName').updateValueAndValidity();
      this.dbConnectionForm.get('parent_id').setValidators(Validators.required);
      this.dbConnectionForm.get('parent_id').updateValueAndValidity();
    } else {
      this.dbConnectionForm.get('dataGroupName').clearValidators();
      this.dbConnectionForm.get('dataGroupName').updateValueAndValidity();
      this.dbConnectionForm.get('parent_id').clearValidators();
      this.dbConnectionForm.get('parent_id').updateValueAndValidity();
    }
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this._connectionDialogRef.close(data);
  }

  /**
   * Navigates the user to the schedular page to create new
   */
  navigateToScheduler() {
    this.closeModal();
    this._spinner.stopSpinner();
    this._router.navigateByUrl('/dashboard/scheduler?type=scheduler');
  }

  /**
   * Custom validator for query string, it returns boolean value
   * @param queryString input query string
   */
  validateQuery(queryString: AbstractControl): void {
    let str: string = queryString.get('query').value;
    if (str?.length) {
      str = str.toLowerCase();
      if (!str.includes('select')) {
        queryString.get('query').setErrors({ queryInvalid: true });
      }
    }
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.connection$) {
      this.connection$.unsubscribe();
    }
  }
}
