import { takeUntil } from 'rxjs/operators';
import { DataFlowService } from './../data-flow.service';
import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { environment } from 'src/environments/environment';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { MatHorizontalStepper } from '@angular/material/stepper';
import { Router } from '@angular/router';

declare let $: any;

@Component({
  selector: 'app-data-flow-stepper',
  templateUrl: './data-flow-stepper.component.html',
  styleUrls: ['./data-flow-stepper.component.scss'],
})
export class DataFlowStepperComponent implements OnInit {
  @Input() flowData: any;
  @Input() dataGroups: Array<any>;
  @Input() schedulars: Array<any>;
  @Input() connId: string;
  @Input() flowIdToEdit: string;
  @Output() closeNodeRed = new EventEmitter<object>();
  @ViewChild('myiframe', { static: false }) iframe: ElementRef;
  @ViewChild(MatHorizontalStepper) stepper: MatHorizontalStepper;

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  serverBaseUrl: string = environment.serverBaseUrl;
  flowFormGroup: FormGroup;
  flowURL: SafeHtml;
  isEdit: boolean = false;
  project: any;
  doc: any;

  constructor(
    private _formBuilder: FormBuilder,
    private _sanitizer: DomSanitizer,
    private _dataFlow: DataFlowService,
    private _toaster: ToastrService,
    private _localStorage: LocalStorageService,
    private _router: Router
  ) {
    this.flowFormGroup = this._formBuilder.group({
      flowName: ['', Validators.required],
      dataGroupId: ['', Validators.required],
      scheduleId: [''],
    });
  }

  /**
   * Initializes the page with required data
   */
  ngOnInit() {
    this.project = this._localStorage.getItem('project');
    this.prefillFlowDetails(this.flowData, this.flowIdToEdit);
    this.flowURL = this._sanitizer.bypassSecurityTrustResourceUrl(`${this.serverBaseUrl}/!/`);
  }

  /**
   * Initializes the node-red for interceptor flow type
   */
  ngAfterViewInit() {
    if (this.connId) {
      this.stepper.next();
    }
  }

  /**
   * Handles the operations when the stepper is closed
   */
  closeStepperHandler(): void {
    if (this.connId || this.flowIdToEdit) {
      this._router.navigateByUrl('/project/data/dataconnection');
    } else {
      this.closeNodeRed.emit({ isCanceled: true });
    }
  }

  /**
   * Prefills the flow details form for edit operation
   * @param flowData
   */
  prefillFlowDetails(flowData: any, flowIdToEdit?: string): void {
    if (flowData || flowIdToEdit) {
      this.isEdit = true;
      this.flowFormGroup.patchValue({
        flowName: flowData ? flowData.flowName : '',
        dataGroupId: flowData ? flowData.dataGroupId : '',
        scheduleId: flowData ? flowData.scheduleId : '',
      });
      this.findOneFlow(flowData ? flowData._id : flowIdToEdit);
    } else {
      this.isEdit = false;
      this.flowFormGroup.reset();
    }
  }

  /**
   * To prohibit user to save a flow without any node in iframe
   */
  validateFlow(): boolean {
    if (window.frames['myiframe'].contentDocument.getElementsByClassName('red-ui-header-toolbar')) {
      let iframeHtml =
        window.frames['myiframe'].contentDocument.getElementsByClassName('red-ui-header-toolbar');
      if (!iframeHtml[0].innerHTML.includes('red-ui-deploy-button disabled')) {
        return false;
      } else {
        return true;
      }
    }
  }

  /**
   * Handles the flow submition as per the functionality - edit/create
   */
  onSubmitFlow(): void {
    // const iframe = document.getElementById("myiframe");
    // const iframe = document.getElementById('myiframe');
    // console.log(" ~ onSubmitFlow ~ iframe", iframe[0]);

    // // console.log(" ~ onSubmitFlow ~ iframe", iframe[0].iframe.contentDocument);
    // // console.log(" ~ onSubmitFlow ~ iframe", iframe[0].contentWindow.document);

    // let btn_deploy = iframe.getElementsByClassName('red-ui-deploy-button-group');
    // // console.log(" ~ createNewFlow ~ btn_deploy", btn_deploy);

    // let urlString = document.getElementById("myiframe")[0].contentWindow.location.href;
    // // console.log(" ~ createNewFlow ~ urlString", urlString);

    // let position = urlString.lastIndexOf("/");
    // let flowId = urlString.substring(position + 1, urlString.length);
    // // btn_deploy.click();

    // // {
    // //   "stack": "Error: Blocked a frame with origin \"http://localhost:4200\" from accessing a cross-origin frame.\n    at o (<anonymous>:1:83)"
    // // }
    // console.log(iframe.querySelectorAll('#red-ui-header'));

    // let od = $('iframe');
    // console.log(" ~ onSubmitFlow ~ od", od);

    // $(od)
    //   .contents()
    //   .find('*')
    //   .each(function (i, item) {
    //     console.log(item);

    //     if ($(item).is('#red-ui-header-button-deploy')) {
    //       console.log('got button');

    //     }
    //   })

    let iframe: any = $('#nodeRedIframe');
    let btn_deploy = iframe.contents().find('#red-ui-header-button-deploy')[0];

    let urlString = iframe[0].contentWindow.location.href;

    let position = urlString.lastIndexOf('/');
    let flowId = urlString.substring(position + 1, urlString.length);
    btn_deploy.click();
    if (flowId == '#' || flowId == '' || undefined) {
      alert('Something went wrong, please refresh');
    }

    let reqObj: any = {
      flowName: this.flowFormGroup.value.flowName,
      flowId: flowId,
      flowType: this.connId ? 'interceptor' : 'direct',
      projectId: this.project._id,
      connectionID: this.connId ? this.connId : null,
      dataGroupId: this.flowFormGroup.value.dataGroupId,
    };
    //if schedulerId is present add the schedulerId key
    this.flowFormGroup.value.scheduleId &&
      (reqObj.scheduleId = this.flowFormGroup.value.scheduleId);

    /** Creates dynamic request object as per the flow types */
    if (this.connId) {
      // delete reqObj.flowName;
      delete reqObj.scheduleId;
      delete reqObj.dataGroupId;
    } else {
      delete reqObj.connectionID;
    }

    if (this.isEdit) {
      reqObj.flowType = this.flowData.flowType;
      if (!this.flowData.dataGroupId) {
        delete reqObj.dataGroupId;
      }
      this.updateFlow(reqObj);
    } else {
      this.createNewFlow(reqObj);
    }
  }

  /**
   * Creates a new UDD flow
   */
  createNewFlow(flowData: any): void {
    this._dataFlow
      .createNewFlow(flowData)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.success('Data flow created successfully', 'Data Flows');
            this.closeNodeRed.emit({ flowResponse: res, isCanceled: true });
          }
        },
        (err: Error) => {
          this._toaster.error(err.message, 'Data Flows');
        }
      );
  }

  /**
   * Updates the flow by triggering API
   * @param flowData
   */
  updateFlow(flowData: any): void {
    let flowIdToUpdate = this.flowData._id || this.flowIdToEdit;
    this._dataFlow
      .updateFlow(flowIdToUpdate, flowData)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.success('Data flow updated successfully', 'Data Flows');
            this.closeNodeRed.emit({ flowResponse: res, isCanceled: true });
          }
        },
        (err: Error) => {
          this._toaster.error(err.message, 'Data Flows');
        }
      );
  }

  /**
   * Fetches the single existing Udd Flow data from DB
   * @param flowId
   */
  findOneFlow(flowId: any): void {
    this._dataFlow
      .getSingleFlow(flowId)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.flowData = res;
            this.flowFormGroup.get('flowName').setValue(res.flowName);
            this.flowFormGroup.get('dataGroupId').clearValidators();
            this.flowFormGroup.get('dataGroupId').updateValueAndValidity();
            this.flowURL = this._sanitizer.bypassSecurityTrustResourceUrl(
              `${this.serverBaseUrl}/!/#flow/${res.flowId}`
            );
          }
        },
        (err: Error) => {
          this._toaster.error('Cannot fetch flows details', 'Data Flows');
        }
      );
  }

  /**
   * Unsubscribes the subscriptions after destroy
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
