import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DataFlowService {
  serverBaseUrl: string = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * Fetches all the flows list from DB
   * @param projectId 
   * @returns 
   */
  getAllFlowsList(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/udd?projectId=${projectId}`);
  }

  /**
   * Creates a new UDD flow 
   * @param flowData 
   * @returns 
   */
  createNewFlow(flowData: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/udd`, flowData);
  }

  /**
   * Deletes the flow from DB
   * @param flowId 
   * @returns 
   */
  deleteFlow(flowId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/udd/${flowId}`);
  }

  /**
   * Fetches the single flow details from DB
   * @param flowId 
   * @returns 
   */
  getSingleFlow(flowId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/udd/${flowId}`);
  }

  /**
   * Updates the flow
   * @param flowId 
   * @param flowData 
   * @returns 
   */
  updateFlow(flowId: string, flowData: any): Observable<any> {
    return this._http.put(`${this.serverBaseUrl}/api/udd/${flowId}`, flowData);
  }

  /**
   * Executes the data flow
   * @param flowId 
   * @param dataGroupId 
   * @returns 
   */
  executeDataFlow(projectId: string, flowId: string, dataGroupId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/project/${projectId}/udd/${flowId}/execute?dataGroupId=${dataGroupId}`);
  }

  /**
   * Fetches the node-red error log from DB
   * @param flowId 
   * @returns 
   */
  getErrorLogs(flowId: string, startDate: string, endDate: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/node-red/errorLog/${flowId}?startDate=${startDate}&endDate=${endDate}`);
  }
}
