  /**
   * Refresh the tree structure to render updated values
   */
  refreshTreeData(): void {
    const data = this.dataSource.data;
    this.dataSource.data = [];
    this.dataSource.data = data;
  }

  /**
   * Handles the click event on tree nodes and binds the base path, with selected folder name
   * @param node selected node
   */
  treeHandler(node: any): any {
    this.nodesArray?.push(node.data.label);
    // console.log(this.nodesArray);
    this.selectedPath = this.nodesArray.toString().split(',').join('/');
    const conn = this.sftpConns.filter(
      (item) => item.name === this.sftpForm.get('connection').value.name
    )[0];
    // console.log(this.selectedPath);
    this.getSFTPFolders(conn._id, this.selectedPath);
    this.sftpForm.get('folderpath').setValue(this.selectedPath);
  }

  resetTreeNode() {
    const conn = this.sftpConns.filter(
      (item) => item.name === this.sftpForm.get('connection').value.name
    )[0];
    // console.log(this.selectedPath);
    this.getSFTPFolders(conn._id, this.selectedPath);
  }
