import { Component, ElementRef, Inject, OnInit, Renderer2, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { DataService } from '../../data.service';
import { DataConnectionComponent } from '../data-connection.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-dialog-handler',
  templateUrl: './dialog-handler.component.html',
  styleUrls: ['./dialog-handler.component.scss'],
})
export class DialogHandlerComponent implements OnInit {
  // list of the available connection labels
  proConnectionList: Array<string> = null;
  enterpriseConnectionList: Array<string> = null;
  connectionsToRestrict: Array<string> = null;
  allConnectionsList: Array<string> = null;

  // saas variables
  allowedNoOfRows: number = 0;
  allowedNoOfColumns: number = 0;
  appType: string = environment.appType;

  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;
  exportProjectDiameter = 50;

  /** Form Groups */
  fileUploadForm: FormGroup;
  dataGroupForm: FormGroup;

  /** Subscription variables */
  dataConnection$: Subscription;

  showSpinner: boolean = false;
  isEdit: boolean = false;
  connectionIdToUpdate: string;
  showConnectionTypes: boolean = true;
  formHeading: string;
  iconType: string;
  sourceType: string;
  project: any;
  dialogHeading: string;
  user: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    _fb: FormBuilder,
    private _data: DataService,
    private _toaster: ToastrService,
    private dialogRef: MatDialogRef<DataConnectionComponent>,
    private _localStorage: LocalStorageService,
    public _utils: UtilsService,
    private renderer: Renderer2,
    private _snackbar: MatSnackBar
  ) {
    this.user = this._localStorage.getItem('user');
    this.enterpriseConnectionList = this.user?.psFeatures?.enterpriseConnectionList || [];
    this.allConnectionsList = this.user?.psFeatures?.allConnectionsList || [];
    this.allowedNoOfColumns = this.user?.psFeatures?.allowedNoOfColumns;
    this.allowedNoOfRows = this.user?.psFeatures?.allowedNoOfRows;
    /** Form instances */
    this.fileUploadForm = _fb.group({
      name: ['', Validators.required],
    });
    this.dataGroupForm = _fb.group({
      name: ['', [Validators.required, Validators.pattern('^[^\\s][A-Za-z0-9_ ]+$')]],
      parentId: ['', Validators.required],
    });
  }

  ngOnInit(): void {
    this.project = this._localStorage.getItem('project');
    this.dialogData.formType != 'dataSource' && this.formUIHandler(this.dialogData.formType);
    switch (this.dialogData.formType) {
      case 'dataSource':
        this.dialogHeading = 'Data Source';
        this.showConnectionTypes = true;
        // init functions for the data connection dialog.
        // console.log(
        //   "this is connection data : ",
        //   this.dialogData?.allowedConnectionsList
        // );
        environment.appType === 'saas' &&
          !this.user?.roles?.includes('super_admin') &&
          !this.user?.roles?.includes('admin') &&
          this.setRestrictions(this.dialogData?.allowedConnectionsList);
        break;
      case 'mysql':
      case 'mssql':
      case 'postgresql':
      case 'sftp':
      case 'url':
      case 's3':
      case 'snowflake':
      case 'bigquery':
        this.sourceType = this.dialogData.formType;
        this.dialogData.formType = 'dataSource';
        this.showConnectionTypes = false;
      case 'dataGroup':
        this.showConnectionTypes = false;
        this.prefillDataGroupForm(this.dialogData.data);
        break;
      default:
        this.prefillDataGroupForm(this.dialogData.data);
    }
  }

  async setRestrictions(allowedConnectionList: Array<string>): Promise<any> {
    //thinking of it not yet completed
    this.connectionsToRestrict = this.allConnectionsList.filter(
      (item) => !allowedConnectionList.includes(item)
    );
    this.proConnectionList = this.connectionsToRestrict.filter(
      (val) => !this.enterpriseConnectionList.includes(val)
    );
    // console.log(
    //   "hi i m inside connections to restrict : ",
    //   this.connectionsToRestrict,
    //   this.proConnectionList,
    //   this.enterpriseConnectionList
    // );
  }

  /**
   * Prefill the data group form for edit functionality
   * @param connectionData selected data group details
   */
  prefillDataGroupForm(dataGroupData: any): void {
    if (dataGroupData) {
      this.isEdit = true;
      this.connectionIdToUpdate = dataGroupData._id;
      this.dataGroupForm.patchValue({
        name: dataGroupData.name,
        parentId: dataGroupData.parentId,
      });
    } else {
      this.dataGroupForm.get('parentId').setValue(this.dialogData.currentDataGroup);
      this.dataGroupForm.get('name').reset();
      this.isEdit = false;
    }
  }

  /**
   * Handles the data group submit functionality as per the operation
   * @param formValue
   */
  onSubmitDataGroup(formValue: any): void {
    if (this.isEdit) {
      this.updateDataGroupHandler(formValue);
    } else {
      this.createDataGroupHandler(formValue);
    }
  }

  /**
   * Updates the data group details
   * @param formValue
   */
  updateDataGroupHandler(formValue: any): void {
    delete this.dialogData.data.name;
    let reqObj: any = { ...this.dialogData.data, name: formValue.name };
    this.dataConnection$ = this._data.updateDataGroup(reqObj).subscribe(
      (res: any) => {
        if (res) {
          this._toaster.success('Folder name updated successfully', 'Data Connection');
          this.closeModal(res);
        }
      },
      (err: Error) => {
        this._toaster.error('Server Error', 'Data Connection');
      }
    );
  }

  /**
   * Creates a new folder
   * @param formValue
   */
  createDataGroupHandler(formValue: any): void {
    let reqObj: any = { ...formValue, projectId: this.project._id };
    this.dataConnection$ = this._data.createNewDataGroup(reqObj).subscribe(
      (res: any) => {
        if (res) {
          this._toaster.success(`${res.name} folder created successfully`, 'Data Connection');
          this.closeModal(res);
        }
      },
      (err: Error) => {
        this._toaster.error('Server Error', 'Data Connection');
      }
    );
  }

  /**
   * Headings and icons handler for forms as per the type
   * @param {*} type - upload, mysql, mssql, postgresql, url, sftp
   */
  formUIHandler(type: string): void {
    if (
      environment.appType === 'enterprise' ||
      this.user?.roles?.includes('super_admin') ||
      this.user?.roles?.includes('admin') ||
      (this.connectionsToRestrict && !this.connectionsToRestrict.includes(type))
    ) {
      this.showConnectionTypes = false;
      this.sourceType = type;
      switch (type) {
        case 'upload':
          this.formHeading = 'Upload a file';
          this.iconType = '../../../../../assets/img/icons/upload.svg';
          break;
        case 'mysql':
          this.formHeading = 'Pull data from MYSQL server';
          this.iconType = '../../../../../assets/img/icons/mysql.svg';
          break;
        case 'mssql':
          this.formHeading = 'Pull data from MSSQL server';
          this.iconType = '../../../../../assets/img/icons/mssql.svg';
          break;
        case 'postgresql':
          this.formHeading = 'Pull data from PostgreSQl server';
          this.iconType = '../../../../../assets/img/icons/postgresql.svg';
          break;
        case 'snowflake':
          this.formHeading = 'Pull from data cloud';
          this.iconType = '../../../../../assets/img/snow.png';
          break;
        case 'sftp':
          this.formHeading = 'Pull data from SFTP server';
          this.iconType = '../../../../../assets/img/icons/sftp.svg';
          break;
        case 'url':
          this.formHeading = 'Data Downloader (Note: Works for dataset < 2MB)';
          this.iconType = '../../../../../assets/img/icons/url.svg';
          break;
        case 's3':
          this.formHeading = 'Pull data from Amazon S3 server';
          this.iconType = '../../../../../assets/img/s3.svg';
          break;
        case 'bigquery':
          this.formHeading = 'Pull data from Google BigQuery';
          this.iconType = '../../../../../assets/img/bigquery.png';
          break;
      }
    } else {
      (this.proConnectionList?.includes(type) || this.enterpriseConnectionList?.includes(type)) &&
        this._snackbar.open('Selected feature is under premium subscription ', 'OK', {
          horizontalPosition: 'right',
          verticalPosition: 'bottom',
        });
    }
  }

  /**
   * Returns the style as per the source type
   * @returns
   */
  getHeadingIconSize(): string {
    switch (this.sourceType) {
      case 'upload':
        return 'height: 20px; width:20px';
      case 'mysql':
        return 'height: 20px; width:20px';

      case 'mssql':
        return 'height: 20px; width:20px';

      case 'postgresql':
        return 'height: 20px; width:20px';

      case 'snowflake':
        return 'height: 20px; width:20px';

      case 'sftp':
        return 'height: 20px; width:20px';

      case 'url':
        return 'height: 20px; width:20px';

      case 's3':
        return 'height: 20px; width:20px';

      case 'bigquery':
        return 'height: 20px; width:20px';
    }
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this.dialogRef.close(data);
  }

  /**
   * Unsubscribes the subscriptions, only if they were subscribed
   */
  ngOnDestroy() {
    if (this.dataConnection$) {
      this.dataConnection$.unsubscribe();
    }
  }
}
