import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DataConnectionComponent } from './data-connection/data-connection.component';
import { RouterModule, Routes } from '@angular/router';
import { DataSetComponent } from './data-set/data-set.component';
import { DataFlowComponent } from './data-flow/data-flow.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { DialogHandlerComponent } from './data-connection/dialog-handler/dialog-handler.component';
import { FileUploadFormComponent } from './data-connection/file-upload-form/file-upload-form.component';
import { DatabaseFormComponent } from './data-connection/database-form/database-form.component';
import { SftpAndUrlFormComponent } from './data-connection/sftp-and-url-form/sftp-and-url-form.component';
import { DataSetDialogComponent } from './data-set/data-set-dialog/data-set-dialog.component';
import { DataFlowStepperComponent } from './data-flow/data-flow-stepper/data-flow-stepper.component';
import { MatBadgeModule } from '@angular/material/badge';
import { AgGridModule } from 'ag-grid-angular';
import { GraphsDialogHandlerComponent } from '../models/graphs-dialog-handler/graphs-dialog-handler.component';
import { TreeModule } from '@circlon/angular-tree-component';

const routes: Routes = [
  {
    path: 'dataconnection',
    component: DataConnectionComponent,
  },
  {
    path: 'dataset',
    component: DataSetComponent,
  },
  {
    path: 'dataflow',
    component: DataFlowComponent,
  },
];

@NgModule({
  declarations: [DataConnectionComponent, DataSetComponent, DataFlowComponent, DialogHandlerComponent, FileUploadFormComponent, DatabaseFormComponent, SftpAndUrlFormComponent, DataSetDialogComponent, DataFlowStepperComponent],
  imports: [
    CommonModule,
    AgGridModule.withComponents([GraphsDialogHandlerComponent]),
    RouterModule.forChild(routes),
    TreeModule,
    SharedModule,
    MatBadgeModule
  ]
})
export class DataModule { }
