import { SchedularService } from './../../dashboard/schedular/schedular.service';
import { SpinnerService } from './../../../shared/spinner.service';
import { Component, OnDestroy, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { DialogHandlerComponent } from './dialog-handler/dialog-handler.component';
import { ThemePalette } from '@angular/material/core';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { DataService } from '../data.service';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { takeUntil } from 'rxjs/operators';
import { UtilsService } from 'src/app/shared/utils.service';
import { MatSnackBar, MatSnackBarRef } from '@angular/material/snack-bar';
import { environment } from 'src/environments/environment';
import { SampleDatasetService } from '../../dashboard/sample-dataset/sample-dataset.service';
import { HttpErrorResponse, HttpEventType } from '@angular/common/http';
import { toasterMsg } from 'src/app/shared/static-data';

const swal = require('sweetalert');

@Component({
  selector: 'app-data-connection',
  templateUrl: './data-connection.component.html',
  styleUrls: ['./data-connection.component.scss'],
  // encapsulation: ViewEncapsulation.None,
})
export class DataConnectionComponent implements OnInit, OnDestroy {
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  filesDataSource: MatTableDataSource<[]> = new MatTableDataSource();
  // fileUploadRef: any;
  @ViewChild(MatPaginator) set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild(MatSort) set sort(value: MatSort) {
    this.dataSource.sort = value;
  }
  @ViewChild('ConnFilesPaginator') set connFilesPaginator(value: MatPaginator) {
    this.filesDataSource.paginator = value;
  }
  @ViewChild('ConnFilesSort') set connFilesSort(value: MatSort) {
    this.filesDataSource.sort = value;
  }
  snackBarRef: MatSnackBarRef<any>;
  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];
  displayedColumnsFiles: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  project: any;
  dataGroupsList: Array<any>;
  connectionsList: any;
  rawfilesList: any;
  rawFilesList: any;
  filesData: any;
  fArr: Array<any>;
  parentFolderData: Array<any>;
  dataList: Array<any>;
  schedularArray: Array<any>;
  tempArray: Array<any> = [];
  user: any;
  breadcrumbArray: Array<any> = [];
  breadArray: Array<any> = [];
  currentDataGroup: string;
  dataListArray: Array<any> = [];
  selectedConnToPull: string;
  isDataFlowFailed: boolean = false;
  isDataFlowPresent: string;
  count: number = 0;
  callFromStartFlow: boolean = false;
  flowIdCheck: string = null;
  dataFlowToolTip: string = 'Pull data';
  urlFlowPlayBtnToolTip: string = 'Execute data flow';
  // this is for the restrictions
  planType: string = null;
  allowedConnectionsList: Array<string> = null;
  appType: string = environment.appType;
  allSampleDatasets: Array<any>;
  filterSampleDatasets: Array<any>;
  percentDone: number = 0;
  datasetDialog: MatDialogRef<DataConnectionComponent>;

  constructor(
    private _toaster: ToastrService,
    private _data: DataService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dialog: MatDialog,
    private _schedular: SchedularService,
    private _window: Window,
    private _snackBar: MatSnackBar,
    private _socket: SocketIoService,
    public _utils: UtilsService,
    private _sampleDataset: SampleDatasetService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.allowedConnectionsList = this.user?.psFeatures?.allowedFeaturesList || [];
    this.displayedColumns = [
      // 'srNo',
      'icon',
      'name',
      'parsed',
      'type',
      'schedular',
      'createdAt',
      'actions',
    ];
    this.displayedColumnsFiles = ['name', 'parsed', 'createdAt', 'actions'];
  }

  /**
   * Initializes page with all the required data
   */
  ngOnInit(): void {
    this._spinner.showSpinner();
    this.initSocket();
    this.getAllConnectionsData();
    this.getDataGroupList();
    this.getAllSampleDatasets();
    this.schedularList();
  }

  /**
   * Fetches all sample dataset list from DB by triggering API
   */
  getAllSampleDatasets(): void {
    // API call
    this._sampleDataset
      .getAllDatasets()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res.data) {
            this.allSampleDatasets = res.data;
            /**
             * Sorts the sample projects list by data in ascending order
             */
            this.filterSampleDatasets = this.allSampleDatasets.sort((a: any, b: any) => {
              return new Date(b.createdAt).valueOf() - new Date(a.createdAt).valueOf();
            });
            this._spinner.stopSpinner();
          }
        },
        (e: any) => {
          this._spinner.stopSpinner();
          this._toaster.error(e.error.message || 'Server Error', 'Project Management');
        }
      );
  }

  /**
   * Imports the sample dataset
   * @param fileName
   */
  importSampleDataset(fileName: any): void {
    this._spinner.showSpinner();
    // API call
    // creating req object for the file upload.
    let reqObj = {
      encoding: 'utf_8',
      projectId: this.project._id,
      dataGroupId: this.currentDataGroup || this.dataList[0]._id,
      dataFolderName: this.currentDataGroup || this.dataList[0]._id,
      parentFolderName: '',
      file: fileName,
      uploadType: 'importDataset',
    };
    this._data.uploadFile(reqObj).subscribe(
      (resp) => {
        this.datasetDialog.close();
        this._spinner.stopSpinner();
        this._utils.showSnackBar(toasterMsg.fileUpload);
      },
      (err: HttpErrorResponse) => {
        this._toaster.error(err.error.message || 'Server Error', 'Data Connection');
        this._spinner.stopSpinner();
        this._utils.dismissSnackBar();
      }
    );
  }

  /**
   * Fetches the connections list from the DB
   */
  getAllConnectionsData(): void {
    let dataFilterArray: Array<any> = [];
    this.dataListArray.length = 0;
    this.breadArray.length = 0;
    this.breadcrumbArray.length = 0;
    this._data
      .getAllConnectionsData(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.dataGroupsList = res[0].datagroupList;
            this.connectionsList = res[0].connection;
            this.rawFilesList = res[0].filesdata;
            this.filesData = res[0].pklfilesdata;
            this.dataListArray = this.dataListArray.concat(this.dataGroupsList, this.connectionsList, this.rawFilesList);
            dataFilterArray = this.dataListArray.filter(
              (temp) => !Object.keys(temp).includes('parentId') && !Object.keys(temp).includes('datagroup_id')
            );
            dataFilterArray = dataFilterArray[0]._id;
            let data = this.dataListArray.filter(
              (temp) => temp.type !== 'upload_retrained' && (temp.parentId === dataFilterArray || temp.datagroup_id === dataFilterArray)
            );
            var filesFilter = data.filter((temp) => !Object.keys(temp).includes('connId'));
            this.parentFolderData = filesFilter;
            this.fArr = filesFilter;
            // Assign the data to the dataSource for the table to show records
            this.dataSource.data = this.fArr;
            this.dataSource._updateChangeSubscription();
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this._spinner.stopSpinner();
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot fetch connections list', 'Data Connection');
          }
          this._utils.dismissSnackBar();
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Data Connection');
        }
      );
  }

  showMessage(message) {
    this.snackBarRef = this._snackBar.open(message, 'OK', {
      verticalPosition: 'bottom',
      horizontalPosition: 'end',
    });
  }
  /**
   * Fetches all the data groups list
   */
  getDataGroupList(): void {
    this._data
      .getDataGroupsList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.dataList = res;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Connection');
        }
      );
  }

  /**
   * Fetches all the schedulars list
   */
  schedularList(): void {
    this._schedular
      .getSchedularsList()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.schedularArray = res;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Connection');
        }
      );
  }

  /**
   * Opens the select dataset dialog
   * @param datasetModal modal reference
   */
  openDatasetModal(datasetModal: TemplateRef<any>) {
    this.datasetDialog = this._dialog.open(datasetModal, {
      minWidth: '50vw',
      maxWidth: '75vw',
    });
  }

  /**
   * Handles the modal opening operation
   * @param type dataSource/dataGroup
   */
  openModalHandler(type: string, data?: any): void {
    const dialogRef = this._dialog.open(DialogHandlerComponent, {
      minWidth: '60vw',
      maxWidth: '60vw',
      data: {
        formType: type,
        dataGroups: this.dataList,
        schedulars: this.schedularArray,
        data: data,
        currentDataGroup: this.currentDataGroup || this.dataList[0]._id,
        // this is the allowed connection list which we are passing to data connection dialog
        allowedConnectionsList: this.allowedConnectionsList || [],
      },
    });
    dialogRef.afterClosed().subscribe((data: any) => {
      if (data) {
        // this.fileUploadRef = data.fileUploadRef;
        this.breadArray.length = 0;
        this.getAllConnectionsData();
        this.getDataGroupList();
        this.schedularList();
      }
    });
  }

  /**
   * Pulls the data for one selected connection at a time
   * @param item
   */
  pullConnectionData(item: any): void {
    this.isDataFlowFailed = false;
    this.callFromStartFlow = false;
    this.flowIdCheck = item.name;
    this._utils.showSnackBar(toasterMsg.pullData);
    this.selectedConnToPull = item._id;
    /** Adds isPulling flag for the selected connection */
    this.fArr = this.fArr.map((doc) => {
      doc._id === item._id ? (doc.isPulling = true) : (doc.isPulling = false);
      return doc;
    });

    // API call
    this._data
      .pullSingleData(item._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            // this.fArr = this.fArr.map((doc) => {
            //   doc._id === item._id ? (doc.isPulling = false) : '';
            //   return doc;
            // });
          } else {
            this.fArr = this.fArr.map((doc) => {
              doc._id === item._id ? (doc.isPulling = false) : '';
              return doc;
            });
          }
          // this._utils.dismissSnackBar();
        },
        (err: Error) => {
          this._utils.dismissSnackBar();
          this.fArr = this.fArr.map((doc) => {
            doc._id === item._id ? (doc.isPulling = false) : '';
            return doc;
          });
        }
      );
  }

  /**
   * Downloads the connection file
   */
  downloadFile(item: any): void {
    this._window.open(`${environment.serverBaseUrl}/api/project/${this.project._id}/download/${item.name}`, '_blank');
  }

  /**
   * Handles the delete operation to delete connection or datagroup
   * @param {connection id} id
   * @param {connection type} type
   */
  deleteHandler(id: string, type: string): void {
    switch (type) {
      case 'upload':
      case 'notebook':
      case 'connectionFile':
        this.deleteRawFile(id, '', type);
        break;
      case 'mysql':
      case 'mssql':
      case 'postgresql':
      case 'sftp':
      case 'url':
      case 's3':
      case 'snowflake':
      case 'bigquery':
        this.deleteDataConnetion(id, type);
        break;
      default:
        this.deleteDataGroup(id);
    }
  }

  /**
   * Deletes the upload and notebook files from DB
   * @param id
   * @param type
   */
  deleteRawFile(id: string, connId?: string, type?: string) {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this file!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      let filteredFiles: Array<any>;
      if (isConfirm) {
        this.breadArray.length = 0;
        this._data
          .deleteRawFile(this.project._id, id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this.getAllConnectionsData();
                this._toaster.success('File deleted successfully', 'Data Connection');
                if (type === 'connectionFile') {
                  filteredFiles = this.rawFilesList.filter((item: any) => {
                    if (item._id !== id && item.connId === connId) {
                      return item;
                    }
                  });
                  // Assign the data to the dataSource for the table to show records
                  this.filesDataSource.data = filteredFiles;
                  this.filesDataSource._updateChangeSubscription();
                  this.filesDataSource.paginator = this.paginator;
                  this.filesDataSource.sort = this.sort;
                } else {
                  filteredFiles = this.fArr.filter(function (item) {
                    return item._id !== id;
                  });
                  // Assign the data to the dataSource for the table to show records
                  this.dataSource.data = filteredFiles;
                  this.dataSource._updateChangeSubscription();
                  this.dataSource.paginator = this.paginator;
                  this.dataSource.sort = this.sort;
                }
              } else {
                this._toaster.error('Cannot delete file', 'Data Connection');
              }
            },
            (err: Error) => {
              this._toaster.error('Server Error', 'Data Connection');
            }
          );
      }
    });
  }

  /**
   * Deletes the folder
   * @param id
   */
  deleteDataGroup(dataId: string): void {
    this.breadArray.length = 0;
    let b: Array<any> = [];
    let rFiles: Array<any> = [];
    let pFiles: Array<any> = [];

    let t = this.dataGroupsList.filter((item: any) => {
      if (item.parentId === dataId || item._id === dataId) {
        this.tempArray.push(item);
      }
      return item.parentId === dataId;
    });

    this.filterDataGroup(t);

    this.connectionsList.map((item: any) => {
      var itemfound = this.tempArray.find((docs: any) => {
        return item.datagroup_id === docs._id;
      });
      if (itemfound !== undefined) {
        let j = item;
        b.push(j);
      }
    });

    this.rawFilesList.map((data: any) => {
      var item = this.tempArray.find((docs: any) => {
        return data.datagroup_id == docs._id;
      });
      if (item !== undefined) {
        let j = data;
        rFiles.push(j);
      }
    });

    this.filesData.map((data: any) => {
      var item = this.tempArray.find((docs: any) => {
        return data.datagroup_id == docs._id;
      });
      if (item !== undefined) {
        var j = data;
        pFiles.push(j);
      }
    });

    /** Alert for confirmation */
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this data!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        let reqObj: any = {
          projectId: this.project._id,
          dataId: dataId,
          datagroup: this.tempArray,
          dataconnection: b,
          rawFiles: rFiles,
          files: pFiles,
        };
        this._data
          .deleteDataGroup(this.project._id, dataId, reqObj)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this._toaster.success(`Folder deleted successfully`, 'Data Connection');
                this.fArr = this.fArr.filter((item: any) => {
                  return item._id != dataId;
                });
                this.dataList = this.dataList.filter((item: any) => {
                  return item._id != dataId;
                });

                // Assign the data to the dataSource for the table to show records
                this.dataSource.data = this.fArr;
                this.dataSource._updateChangeSubscription();
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
              } else {
                this._toaster.error('Cannot delete folder', 'Data Connection');
              }
            },
            (err: Error) => {
              this._toaster.error('Server Error', 'Data Connection');
            }
          );
      }
    });
  }

  /**
   * Deletes the data connection from DB
   * @param id
   * @param type
   */
  deleteDataConnetion(id: string, type: string): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this data!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.breadArray.length = 0;
        this._data
          .deleteDataConnection(this.project._id, id)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this._toaster.success(`${type.toUpperCase()} connection deleted successfully`, 'Data Connection');
                this.fArr = this.fArr.filter((item: any) => {
                  return item._id != id;
                });
                // Assign the data to the dataSource for the table to show records
                this.dataSource.data = this.fArr;
                this.dataSource._updateChangeSubscription();
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
              } else {
                this._toaster.error('Cannot delete file', 'Data Connection');
              }
            },
            (err: Error) => {
              this._toaster.error('Server Error', 'Data Connection');
            }
          );
      }
    });
  }

  /**
   * Opens the connection files modal pop-up
   * @param type
   * @param data
   */
  openFilesModal(connectionFilesTemplate: TemplateRef<any>, data: any): void {
    this.isDataFlowPresent = data.dataflowId;
    let filteredFiles: Array<any>;
    if (data.type !== 'upload' && data.type !== 'notebook') {
      filteredFiles = this.rawFilesList.filter((item: any) => {
        if (item.connId === data._id && !item.isDeleted) {
          return item;
        }
      });
      // Assign the data to the dataSource for the table to show records
      this.filesDataSource.data = filteredFiles;
      this.filesDataSource._updateChangeSubscription();
      this._dialog.open(connectionFilesTemplate, {
        height: 'auto',
        width: '60%',
      });
    }
  }

  /**
   * Executes the data flow
   * @param fileData
   */
  executeDataFlow(fileData: any): void {
    this.callFromStartFlow = true;
    this.flowIdCheck = fileData.name;
    this._data
      .executeDataFlow(this.project._id, fileData.connId, fileData.name)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.success('Flow execution started', 'Data Connection');
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Connection');
        }
      );
  }

  /**
   * Handles the breadcrumbs
   * @param dataGroupId
   * @param name
   * @param isChanged whether any breadcrumb is clicked to navigate
   */
  breadcrumbHandler(dataGroupDetails: any, isChanged?: boolean): void {
    if (!dataGroupDetails.type) {
      let dataGroupId = dataGroupDetails._id || dataGroupDetails.id;
      this._spinner.showSpinner();
      this.currentDataGroup = dataGroupId;
      let data: Array<any> = [];
      let filesFilter: Array<any> = [];
      /** If bread crum is clicked, then navigates to selected folder else create new breadcrumb */
      if (isChanged) {
        let index = this.breadArray.findIndex((item) => {
          return item.id == dataGroupId;
        });
        this.breadArray.splice(index + 1);
        /** If clicked on root breadcrumb */
        if (!dataGroupDetails) {
          this.getAllConnectionsData();
        }
      } else {
        this.breadArray.push({
          id: dataGroupId,
          name: dataGroupDetails.name,
        });
      }
      this.breadcrumbArray = this.breadArray;
      data = this.dataListArray.filter((item) => {
        return item.parentId === dataGroupId || item.datagroup_id === dataGroupId;
      });

      filesFilter = data.filter((temp) => {
        return !Object.keys(temp).includes('connId');
      });
      this.fArr = filesFilter;
      // Assign the data to the dataSource for the table to show records
      this.dataSource.data = filesFilter;
      this.dataSource._updateChangeSubscription();
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this._spinner.stopSpinner();
    }
  }

  /**
   * Filtering required while deleting data group
   * @param id
   */
  filterDataGroup(id: any): void {
    let d: any;
    for (var i = 0; i < id.length; i++) {
      d = this.dataGroupsList.filter((item) => {
        if (item.parentId == id[i]._id) {
          this.tempArray.push(item);
        }
        return item.parentId == id[i]._id;
      });
      this.callRecursive(d);
    }
  }

  /**
   * Recursive function
   * @param d
   */
  callRecursive(d: any): void {
    if (d.length > 0) {
      this.filterDataGroup(d);
    } else {
      return;
    }
  }

  /**
   * Returns the icon class as per the connection type
   * @param type
   */
  getIcon(type: string): string {
    switch (type) {
      case 'mysql':
      case 'mssql':
      case 'postgresql':
      case 'sftp':
      case 's3':
      case 'snowflake':
      case 'bigquery':
        return 'fa fa-database icons';

      case 'upload':
      case 'notebook':
        return 'fa fa-file-alt icons';

      case 'url':
        return 'fa fa-link icons';

      default:
        return 'fa fa-folder icons';
    }
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event, tableName: string): void {
    const filterValue = (event.target as HTMLInputElement).value;
    if (tableName === 'connections') {
      this.dataSource.filter = filterValue.trim().toLowerCase();
      if (this.dataSource.paginator) {
        this.dataSource.paginator.firstPage();
      }
    } else if (tableName === 'connectionFiles') {
      this.filesDataSource.filter = filterValue.trim().toLowerCase();
      if (this.filesDataSource.paginator) {
        this.filesDataSource.paginator.firstPage();
      }
    }
  }

  /**
   * Initializes the socket to listen events when emmited
   */
  initSocket(): void {
    // Listens to the pull data event
    this._socket.listen('pullData').subscribe(
      (res: any) => {
        if (res.status === 'pull_success') {
          this._utils.dismissSnackBar();
          if (res.data.parsed) {
            res.data.projectId === this.project._id && this._toaster.success('Data uploaded successfully', 'Data Connection');
          } else {
            res.data.projectId === this.project._id && this._toaster.info(res.data.message, 'Data Connection');
          }
          this.getAllConnectionsData();
        } else {
          // Stops the spinner of selected connection if error
          this.fArr = this.fArr.map((doc) => {
            if (doc._id === this.selectedConnToPull) {
              doc.isPulling = false;
              (doc.type === 'sftp' || doc.type === 's3') && res.status === 'No updated files'
                ? this._toaster.info(res.status, 'Data Connection')
                : this._toaster.error(res.status, 'Data Connection');
            }
            return doc;
          });
          res.status === 'No updated files'
            ? this._toaster.info(res.status.message || res.status || 'Could not complete the operation', 'Data Connection')
            : this._toaster.error(res.status.message || res.status || 'Could not complete the operation', 'Data Connection');
          this._spinner.stopSpinner();
          this._utils.dismissSnackBar();
        }
        this._utils.dismissSnackBar();
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._utils.dismissSnackBar();
        this._toaster.error('Socket Error', 'Data Connection');
      }
    );

    // Listens to node-red event
    this._socket.listen('nodeRedFlowExecution').subscribe(
      (res: any) => {
        if (res.status === 'flow_execution_failed') {
          this.isDataFlowFailed = true;
          this.dataFlowToolTip = 'Data Flow Execution Failed';
          this.urlFlowPlayBtnToolTip = 'Data Flow Execution Failed';
        }
        if (this.isDataFlowFailed) {
          if (this.count < 1) {
            this._toaster.error('Data Flow Execution Failed', 'Data Flow');
          }
          this.count++;
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Socket Error', 'Data Connection');
      }
    );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this._socket.removeListener('pullData');
    this._socket.removeListener('nodeRedFlowExecution');
    this._utils.dismissSnackBar();
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
