import { SchedularService } from './../../dashboard/schedular/schedular.service';
import { DataFlowService } from './data-flow.service';
import {
  Component,
  OnInit,
  ViewChild,
  TemplateRef,
  AfterViewInit,
  ViewEncapsulation,
} from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { takeUntil } from 'rxjs/operators';
import { MatDialog } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../data.service';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { UtilsService } from 'src/app/shared/utils.service';

const swal = require('sweetalert');
const moment = require('moment');
@Component({
  selector: 'app-data-flow',
  templateUrl: './data-flow.component.html',
  styleUrls: ['./data-flow.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class DataFlowComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  logsDataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild('flowPaginator') set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild('dataflowSort') set sort(value: MatSort) {
    this.dataSource.sort = value;
  }

  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];
  logsDisplayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  project: any;
  user: any;
  showStepper: boolean = false;
  hideDialog: boolean = true;
  uddFlowsList: Array<any>;
  inputFlowData: any;
  dataGroupList: Array<any>;
  schedularsList: Array<any>;
  errorLogs: Array<any>;
  selectedFlow: any;
  dateGroup: FormGroup;
  dateRange: any = {};
  connId: string;
  flowIdToEdit: string;
  isDataFlowFailed: boolean = false;
  directFlowNameCheck: string = null;

  constructor(
    private _toaster: ToastrService,
    private _data: DataService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dataFlow: DataFlowService,
    private _schedular: SchedularService,
    private _dialog: MatDialog,
    private _activatedRoute: ActivatedRoute,
    private _socket: SocketIoService,
    private _router: Router,
    public _utils: UtilsService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    /** Handles the stepper when user try to create/update flow from data-connection page */
    if (this._activatedRoute.queryParams) {
      this._activatedRoute.queryParams.subscribe((val) => {
        if (val.connectionId) {
          this.connId = val.connectionId;
          this.showStepper = true;
        } else if (val.dataFlowId) {
          this.flowIdToEdit = val.dataFlowId;
          this.showStepper = true;
        } else {
          this.showStepper = false;
        }
      });
    }
    this.displayedColumns = ['srNo', 'flowName', 'flowId', 'flowType', 'actions'];
    this.logsDisplayedColumns = ['date', 'level', 'node', 'message'];
    /** Date input form instance */
    this.dateGroup = new FormGroup({
      start: new FormControl('', Validators.required),
      end: new FormControl('', Validators.required),
    });
  }

  /**
   * Assigns the references after the view is initialized
   */
  // ngAfterViewInit() {
  //   this.dataSource.paginator = this.paginator;
  //   this.dataSource.sort = this.sort;
  // }

  /**
   * Initialize page with all the required data
   */
  ngOnInit(): void {
    this._spinner.showSpinner();
    this.initSocket();
    this.getFlowsList();
    this.getDataGroupList();
    this.schedularList();
    this.dateGroup.get('start').setValue(moment().subtract(3, 'days'));
    this.dateGroup.get('end').setValue(moment());
  }

  /**
   * Fetches all the flows list from DB
   */
  getFlowsList(): void {
    this._dataFlow
      .getAllFlowsList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.uddFlowsList = res;
            this.dataSource.data = res;
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.dataSource._updateChangeSubscription();
            console.log(' ~ this.dataSource', this.dataSource);
            this._spinner.stopSpinner();
          } else {
            this._spinner.stopSpinner();
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Data Flows');
        }
      );
  }

  /**
   * Fetches all the data groups list
   */
  getDataGroupList(): void {
    this._data
      .getDataGroupsList(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.dataGroupList = res;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Flows');
        }
      );
  }

  /**
   * Fetches all the schedulars list
   */
  schedularList(): void {
    this._schedular
      .getSchedularsList()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.schedularsList = res;
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Flows');
        }
      );
  }

  /**
   * Starts the execution of the flow
   * @param uddFlow
   */
  startFlow(flowData: any): void {
    this.directFlowNameCheck = flowData.flowId;
    this._dataFlow
      .executeDataFlow(this.project._id, flowData._id, flowData.dataGroupId)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._toaster.success('Flow execution started', 'Data Flows');
          }
        },
        (err: Error) => {
          this._toaster.error(err.message, 'Data Flows');
        }
      );
  }

  /**
   * Called when the start/end date is changed
   * @param type
   * @param value
   */
  dateChanged(type: string, value: any): void {
    if (type === 'start') {
      this.dateRange.startDate = value;
    } else {
      this.dateRange.endDate = value;
    }
    if (this.dateRange.endDate) {
      this.getNodeRedLogs(this.selectedFlow._id, this.dateRange.startDate, this.dateRange.endDate);
    }
  }

  /**
   * Get the list of logs present in the auditlog from DB and renders it in the dialog
   * @param flowData
   * @param errorLogsTemplate
   */
  viewNodeRedLogs(flowData: any, errorLogsTemplate: TemplateRef<any>): void {
    this.selectedFlow = flowData;
    let startDate = this.dateGroup.get('start').value;
    let endDate = this.dateGroup.get('end').value;
    this.getNodeRedLogs(flowData._id, startDate, endDate);
    this.hideDialog = false;
    this._dialog.open(errorLogsTemplate, {
      height: 'auto',
      width: '60%',
    });
  }

  /**
   * Fetches the error logs from DB as per the specified date
   * @param flowId
   * @param startDate
   * @param endDate
   */
  getNodeRedLogs(flowId: string, startDate: string, endDate: string): void {
    this._dataFlow
      .getErrorLogs(flowId, moment.utc(startDate).format(), moment.utc(endDate).format())
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.errorLogs = res;
            this.logsDataSource.data = this.errorLogs;
            this.logsDataSource.paginator = this.paginator;
            this.logsDataSource.sort = this.sort;
          } else {
            this._toaster.error('Cannot fetch error logs', 'Data Flows');
          }
        },
        (err: Error) => {
          this._toaster.error('Server Error', 'Data Flows');
        }
      );
  }

  /**
   * Opens the stepper to edits the flow
   * @param flowData
   */
  editFlow(flowData: any): void {
    this.showStepper = true;
    this.inputFlowData = flowData;
  }

  /**
   * Deletes the data flow
   * @param {connection id} id
   * @param {connection type} type
   */
  deleteHandler(flowId: string): void {
    swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this data flow!',
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this._dataFlow
          .deleteFlow(flowId)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this.uddFlowsList = this.uddFlowsList.filter((item: any) => item._id !== flowId);
                this.dataSource.data = this.uddFlowsList;
                this.dataSource._updateChangeSubscription();
                this.dataSource.paginator = this.paginator;
                this.dataSource.sort = this.sort;
                this._toaster.success(`${res.flowName} deleted successfully`, 'Data Flows');
              } else {
                this._toaster.error('Sorry! Cannot delete the flow', 'Data Flows');
              }
            },
            (err: Error) => {
              this._toaster.error('Server Error', 'Data Flows');
            }
          );
      }
    });
  }

  /**
   * Function is called when node-red stepper is closed or cancelled
   * @param event
   */
  nodeRedClosed(event: any): void {
    if (event.flowResponse) {
      this.showStepper = !event.isCanceled;
      this.flowIdToEdit = '';
      this.connId = '';
      this.inputFlowData = [];
      this._spinner.stopSpinner();
      this._router.navigate(['/project/data/dataflow']);
      this.getFlowsList();
    } else {
      this.flowIdToEdit = '';
      this.connId = '';
      this.inputFlowData = [];
      this.showStepper = !event.isCanceled;
    }
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Initializes the socket to listen events when emmited
   */
  initSocket(): void {
    // Listens to the pull data event
    this._socket.listen('pullData').subscribe(
      (res: any) => {
        this._spinner.stopSpinner();
        if (res.status === 'pull_success') {
          (res.data.projectId === this.project._id) && this._toaster.success('Flow execution completed successfully', 'Data Flows');
          (res.data.projectId === this.project._id) && this._router.navigate(['/project/data/dataset']);
        } else {
          (res.data.projectId === this.project._id) && this._toaster.success(res.err_msg, 'Data Flows');
        }
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Socket Error', 'Data Flows');
      }
    );

    // Listens to node-red event
    this._socket.listen('nodeRedFlowExecution').subscribe(
      (res: any) => {
        if (res.status === 'flow_execution_failed') {
          this.isDataFlowFailed = true;
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Socket Error', 'Data Connection');
      }
    );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this._socket.removeListener('pullData');
    this._socket.removeListener('nodeRedFlowExecution');
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
