import { ReportsService } from './../../dashboard/reports/reports.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ThemePalette } from '@angular/material/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { ProgressSpinnerMode } from '@angular/material/progress-spinner';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { EdaReportComponent } from 'src/app/shared/components/eda-report/eda-report.component';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { DataSetDialogComponent } from './data-set-dialog/data-set-dialog.component';
import { DomSanitizer } from '@angular/platform-browser';
import { DataService } from '../data.service';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { takeUntil } from 'rxjs/operators';
import { UtilsService } from 'src/app/shared/utils.service';
import { GridOptions } from 'ag-grid-community';

const swal = require('sweetalert');
@Component({
  selector: 'app-data-set',
  templateUrl: './data-set.component.html',
  styleUrls: ['./data-set.component.scss'],
})
export class DataSetComponent implements OnInit {
  /** Data table settings */
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  @ViewChild(MatPaginator) set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  @ViewChild(MatSort) set sort(value: MatSort) {
    this.dataSource.sort = value;
  }

  pageSizeOptions: Array<number> = [10, 15, 20];
  displayedColumns: Array<string> = [];

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  gridOptions: GridOptions;
  resizeEvent = 'resize.ag-grid';
  /** Progress spinner settings */
  color: ThemePalette = 'primary';
  mode: ProgressSpinnerMode = 'indeterminate';
  diameter = 100;

  project: any;
  user: any;
  showSpinner: boolean = false;
  breadcrumbArray: Array<any> = [];
  breadArray: Array<any> = [];
  fArr: Array<any> = [];
  dataFilterArray: Array<any> = [];
  previewData: any;

  constructor(
    private _toaster: ToastrService,
    private _data: DataService,
    private _localStorage: LocalStorageService,
    private _spinner: SpinnerService,
    private _dialog: MatDialog,
    private _report: ReportsService,
    private _sanitizer: DomSanitizer,
    private _socket: SocketIoService,
    public _utils: UtilsService
  ) {
    this.project = this._localStorage.getItem('project');
    this.user = this._localStorage.getItem('user');
    this.displayedColumns = [
      // 'srNo',
      'icon',
      'fileName',
      'createdAt',
      'noOfRecords',
      'noOfColumns',
      'fileSize',
      'actions',
    ];
  }

  /**
   * Initializes page with all the required data
   */
  ngOnInit(): void {
    this._spinner.showSpinner();
    this.initSocket();
    this.getDataSetList();
  }

  /**
   * Fetches the data set list
   */
  getDataSetList(): void {
    let dataListArrayId: string;
    let dataListArray: Array<any> = [];
    this.breadArray.length = 0;
    this.breadcrumbArray.length = 0;
    this._data
      .getAllProjectDatas(this.project._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            let data: Array<any> = [];
            this.dataFilterArray = [];
            this.dataFilterArray = this.dataFilterArray.concat(
              res[0].dataGroupList,
              res[0].filesList
            );
            dataListArray = this.dataFilterArray.filter(
              (temp) =>
                !Object.keys(temp).includes('parentId') &&
                !Object.keys(temp).includes('datagroup_id')
            );
            dataListArrayId = dataListArray[0]._id;
            data = this.dataFilterArray.filter(
              (temp) =>
                temp.fileSource != 'upload_retrained' &&
                (temp.parentId == dataListArrayId || temp.datagroup_id == dataListArrayId)
            );
            this.fArr = data;
            // Assign the data to the dataSource for the table to show records
            this.dataSource.data = data;
            this.dataSource._updateChangeSubscription();
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this._spinner.stopSpinner();
          } else {
            this._spinner.stopSpinner();
            this._toaster.error('Cannot fetch data list', 'Data Sets');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Data Sets');
        }
      );
  }

  /**
   * Handles the delete operation to delete connection or datagroup
   * @param {connection id} id
   * @param {connection type} type
   */
  deleteHandler(dataSetId: string): void {
    let content = document.createElement('div');
    content.innerHTML =
      'You will not be able to recover <b>' +
      'Eda, Training and Models ' +
      '</b> related to this dataset!';
    swal({
      title: 'Are you sure?',
      content: content,
      icon: 'warning',
      buttons: {
        cancel: {
          text: 'Cancel',
          value: null,
          visible: true,
          className: '',
        },
        confirm: {
          text: 'Yes, delete it!',
          value: true,
          visible: true,
          className: 'bg-danger',
        },
      },
    }).then((isConfirm: boolean) => {
      if (isConfirm) {
        this.breadArray.length = 0;
        this._data
          .deleteDataSet(this.project._id, dataSetId)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe(
            (res: any) => {
              if (res) {
                this.getDataSetList();
                this._toaster.success('File deleted successfully', 'Data Sets');
              } else {
                this._toaster.error('Cannot delete file', 'Data Sets');
              }
            },
            (err: Error) => {
              this._toaster.error('Server Error', 'Data Sets');
            }
          );
      }
    });
  }

  /**
   * Opens the dialog
   * @param type preview/report/merge
   */
  openDialog(data: any, type: string, columns?: Array<any>): void {
    let dialogWidth = type === 'preview' ? '80vw' : '50vw';
    let dialodRef = this._dialog.open(DataSetDialogComponent, {
      minWidth: dialogWidth,
      maxWidth: '80vw',
      data:
        type === 'preview'
          ? { type: type, dataId: columns, reqObj: data }
          : {
              type: type,
              columns: columns,
              data: data,
            },
    });

    dialodRef.afterClosed().subscribe((data) => {
      if (data) {
        this.getDataSetList();
      }
    });
  }

  /**
   * Fetches the file data and renders in dialog
   * @param type
   * @param data
   */
  openDataPreviewModal(data: any, type: string): void {
    this.showSpinner = true;
    let reqObj: object = {
      dataId: data._id,
      pid: this.project._id,
    };
    this.showSpinner = false;
    this.openDialog(reqObj, 'preview', data._id);
  }

  /**
   * Opens the EDA report dialog to render the details
   * @param fileData
   * @param type
   */
  openDataReportModal(fileData: any, type: string): void {
    let edaReportData: object = {
      projectId: this.project._id,
      dataId: fileData._id,
    };
    /** Opens the eda report component modal to render report */
    this._dialog.open(EdaReportComponent, {
      minHeight: '90vh',
      maxHeight: '90vh',
      minWidth: '90vw',
      data: {
        moduleName: 'data-set',
        edaReportData: edaReportData,
      },
    });
  }

  /**
   * Opens the dialog with form to merge the data
   * @param parentDataSetId
   * @param datagroup
   */
  openMergeDataModal(fileData: any, type: string): void {
    this.openDialog(fileData, type);
  }

  /**
   * Handles the breadcrumbs
   * @param dataGroupId
   * @param name
   * @param isChanged whether any breadcrumb is clicked to navigate
   */
  breadcrumbHandler(dataGroupDetails: any, isChanged?: boolean): void {
    if (!dataGroupDetails.fileSource) {
      let dataGroupId = dataGroupDetails._id || dataGroupDetails.id;
      this.showSpinner = true;
      let data: Array<any> = [];
      let filesFilter: Array<any> = [];
      /** If bread crum is clicked, then navigates to selected folder else create new breadcrumb */
      if (isChanged) {
        let index = this.breadArray.findIndex((item) => {
          return item.id == dataGroupId;
        });
        this.breadArray.splice(index + 1);
        /** If clicked on root breadcrumb */
        if (!dataGroupDetails) {
          this.getDataSetList();
        }
      } else {
        this.breadArray.push({
          id: dataGroupId,
          name: dataGroupDetails.name,
        });
      }
      this.breadcrumbArray = this.breadArray;
      data = this.dataFilterArray.filter((item) => {
        return item.parentId === dataGroupId || item.datagroup_id === dataGroupId;
      });

      filesFilter = data.filter((temp) => {
        return !Object.keys(temp).includes('connId');
      });
      this.fArr = filesFilter;
      // Assign the data to the dataSource for the table to show records
      this.dataSource.data = filesFilter;
      this.dataSource._updateChangeSubscription();
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.showSpinner = false;
    }
  }

  /**
   * Called when any string is entered in search box
   * @param event keyup event
   */
  applyFilter(event: Event): void {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /**
   * Initializes the socket to listen events when emmited
   */
  initSocket(): void {
    this._socket.listen('merge').subscribe(
      (res: any) => {
        if (res.status === 'pull_success') {
          this.getDataSetList();
          this._toaster.success('Data merged successfully', 'Data Set');
        } else {
          this._toaster.error(res.status, 'Data Set');
        }
        this._spinner.stopSpinner();
      },
      (err) => {
        this._spinner.stopSpinner();
        this._toaster.error('Socket Error', 'Data Set');
      }
    );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this._socket.removeListener('pullData');
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
